.other-announce {
    padding: 6.3rem 4.6rem;
    &__content {
        border-radius: 0.5rem;
        background-color: #fff;
        padding: 4rem;
        font-size: 2rem;
        line-height: 1.5;
        width: 100%;
        border: 0;
        border-radius: 5px;
        overflow: hidden;
    }
}

.other-message {
    &__table {
        margin-bottom: 3rem;
        th {
            top: 0 !important;
        }
        td {
            &:first-child {
                padding-left: 10rem;
            }
        }
    }
}